:root {
	--mdc-theme-primary: rgb(0, 142, 231); /* The theme primary color */
	--mdc-theme-secondary: rgb(169, 49, 182); /* The theme secondary color */
	--mdc-theme-background: #fff; /* The theme background color */
	--mdc-theme-surface: #fff; /* The theme surface color */
	--mdc-theme-on-primary: #fff; /* Text color on top of a primary background */
	--mdc-theme-on-secondary: #fff; /* Text color on top of a secondary background */
	--mdc-theme-on-surface: #000; /* Text color on top of a surface background */
}

.root {
	composes: mdc-button from '@material/button/dist/mdc.button.css';
}

.raised {
	composes: mdc-button--raised from '@material/button/dist/mdc.button.css';
}

.unelevated {
	composes: mdc-button--unelevated from '@material/button/dist/mdc.button.css';
}

.outlined {
	composes: mdc-button--outlined from '@material/button/dist/mdc.button.css';
}

.dense {
	composes: mdc-button--dense from '@material/button/dist/mdc.button.css';
}



